body{
  padding: 10px;
}
h1,h2,h3,h4,h5,h6{
  margin: 0;
  padding: 0;
}
.app__container{
  max-width: 900px;
  margin: 0 auto;
  padding: 0 10px;
}
.app__fields{
  margin: 0 0 30px 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.app__field{
  width: calc(100% - 25px);
  padding: 10px;
  border: 2px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  font-size: 16px;
  transition: border-color 0.3s, box-shadow 0.3s;
  background-color: #fff;
}


.app__field:focus {
  border-color: #007BFF;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
  outline: none;
}

textarea.app__field {
  resize: vertical;
  min-height: 100px;
}
.app__buttons{
  display: flex;
}
.app__save{
 flex: 1 1 auto;
  margin: 0 10px 0 0;
}
.app__save button{
  background-color: #28a745;
}
.app__generate button{
  background-color: #2892a7;
}
.app__buttons button {
  padding: 10px 10px;
  font-size: 16px;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
  box-shadow: 0 2px 5px rgba(0, 123, 255, 0.2);
  width: 100%;
}

.app__save button:hover {
  background-color: #218838;
  transform: translateY(-2px);
}

.app__save button:active {
  transform: translateY(1px);
  box-shadow: 0 1px 3px rgba(0, 123, 255, 0.2);
}



.app__todos-list{
  overflow-y: auto;
  max-height: 100%;
  margin: 15px 0 0 0;

}
.app__todos-list span{
  font-size: 18px;
  line-height: 130%;
}
.total-found{
  margin: 0 0 5px 0;
}

.app__todos{
  display: flex;
  gap: 20px;  
}
.app__todos-list{
  max-height: 400px;
}
.app__todos-column:nth-child(1){
  width: 200px;
}
.app__todos-column:nth-child(2){
  flex: 1 1 auto;
  
}
.app__todos-list .todo{
  margin: 0 0 10px 0;
}
.app__todos-list .todo:last-child{
  margin: 0 ;
}
.app__add{
  margin: 60px 0 0 0;
}
.app__filters{
  margin: 20px 0 0 0;
}
.app__add-title{
  text-transform: uppercase;
  font-size: 23px;
  margin: 0 0 20px 0;
}
.severity{
  display: flex;
}

.severity div{
  background-color: #f5f5f5;
  padding: 10px 20px;
  font-size: 17px;
  margin: 0 10px 0 0;
}

.severity label{
  position: relative;
  cursor: pointer;
  padding: 10px 20px;
  background-color: #f5f5f5;
  transition: background-color 0.3s, color 0.3s;
}
.severity label._checked{
  background-color: #0e0e0e;
  color: #fff;
}

.severity label input[type="radio"]{
  display: none;
}

.notFoundImage, .addTask {
  text-align: center;
  margin: 25px 0 0 0;
}
.addTask{
  margin: 50px 0 0 0;
}
.notFoundImage-text, .addTask-text{
  font-size: 25px;
  position: relative;
  z-index: 2;
  font-weight: 500;
}
.notFoundImage img, .addTask img{
  width: 300px;
  position: relative;
  top: -5px;

}


@media(max-width:800px){
  .app__todos{
   flex-direction: column;
   height: auto;
  }
  .app__todos-column{
    width: 100% !important; 
  }
  .app__todos-list{
    max-height: 500px;
  }
  .app__todos-column:nth-child(2){
    flex: 0 0 auto;
  }
  .app__filters{
    display: flex;
    gap: 20px;
  }
  .total-found{
    display: none;
  }
  .app__add{
    margin: 50px 0 0 0;
  }

}

@media(max-height:1120px){
  .app__todos-list{
    max-height: 400px;
  }
}
@media(max-height:1020px){
  .app__todos-list{
    max-height: 340px;
  }
}
@media(max-width:640px){
  .app__filters{
    justify-content: center;
    flex-wrap: wrap;
  }
}