.todo{
    padding: 10px;
    position: relative;
    overflow: hidden;
    border: 1px solid #ccc;
}
.todo._done{
    background-color: #f5f5f5;
}
.todo__delete-button{
    height: 100%;
    position: absolute;
    top: 50%;
    right: -150px;
    transform: translateY(-50%);
    padding: 10px 15px;
    background-color: red;
    color: white;
    border: none;
    cursor: pointer;
    transition: right 0.5s ease-in;
    font-size: 16px;
    width: 150px;
}

@media only screen and (min-width: 769px){
    .todo:hover .todo__delete-button{
        right: 0;
    }
}

@media only screen and (max-width: 768px) {
    .todo._active .todo__delete-button {
        right: 0;
    }
  }
.todo__checkbox{
    width: 20px;
}

.todo__container{
    display: flex;
}
.todo__column:nth-child(2){
    margin-left: 10px;
    flex: 1 1 50%;
}
.todo__description{
    color: #8d8d8d;
    max-width: 90%;
    word-break: break-all;
}
.todo__severity{
    background-color: #e0e0e0;
    font-size: 14px;
    display: inline-block;
    padding: 5px 10px;
    margin: 10px 0 0 0;
}
.todo._done .todo__severity{
    background-color: #cdcccc;;
}
.todo__date{
    font-size: 14px;
    color: #b1b1b1;
    font-weight: 300;
}

@media (max-width:500px) {
    .todo__column:nth-child(3){
        display: none;
    }
}